<template lang="html">
  <div class="">
    <h3 style='margin-bottom:60px;'>Choose your favorite photo</h3>
    <div v-for='choice of choices' class="eachchoice">
      <input type="radio" :id="choice.Choice" name='opts' v-model='fav' :value="choice.value" style='display:none'>
      <label :for="choice.Choice">
        <img v-if='choice.value == fav' class='chosenimg' :src="choice.fig" alt="choice.value">
        <img v-else :src="choice.fig"  alt="choice.value">
      </label>
    </div>
    <p v-if='this.fav != ""'>You chose: {{fav}}</p>
  </div>
</template>

<script>
export default {
  data(){
    return{
      choices:[{fig:require('./../assets/kw.jpg'),value:'Kate Winslet',Choice:1},
      {fig:require('./../assets/gf.jpg'),value:'The Godfather',Choice:2},
      {fig:require('./../assets/cd.jpg'),value:'Cameron Diaz',Choice:3}
      ],
      fav:''
    }
  },
  methods:{
  }
}
</script>

<style lang="css" scoped>
.eachchoice{
  display: inline-block;
  margin: auto 20px;
}
.chosenimg{
  border: 8px solid purple;
  box-sizing: border-box;
}
</style>



